import util from '../common/util.js'
export default {
	name:"km-order",
	props:{
		name:{
			default:""
		},
		status:{		////0:未接单；1:待确定；2：已退款；3：运输中；4：已完成；s-0：货主已发布；s-4：货主已完成
			default:'1'		// 0:审核中；1:未接单；2：拒绝；3:待确定；4：运输中（出货）；5：已完成（收货）；6 已支付；7：待支付
		},
		hideName:{	//是否过滤名字
			default: 'true'
		},
		isRemark:{	//是否评论
			default: false
		},
		userType:{	//用户是否是车主
			default: 1
		},
		startCity:{	//开始城市
			default:''
		},
		endCity:{	//终点城市
			default:''
		},
		money:{	//佣金
			default:''
		},
		carType:{	//车辆要求
			default:''
		},
		goodsWeight:{	//货重
			default:''
		},
		goodsName:{	//货品名
			default:''
		},
		startTime:{	//装货时间
			default:''
		},
		createTime:{	//发布时间
			default:''
		},
		orderId:{
			default:0
		},
		needCar:{
			default:0
		}
	},
	data() {
		return {
			propagations:true,
			isDel:true
		};
	},
	computed:{
		filterName: function(){
			return this.name.slice(0,1)+"**";
		},
		filterStartTime:function() {
			var isToday = util.contrastDate(this.startTime);
			var str = this.startTime;
			if(isToday){
				str = '今天'
			} else {
				str = util.formatTime(this.startTime);
			}
			return str;
		},
		filterCreateTime:function() {
			var isToday = util.contrastDate(this.createTime);
			var str = this.createTime;
			if(isToday){
				str = '今天'
			} else {
				str = util.formatTime(this.createTime);
			}
			return str;
		}
	},
	methods:{
		seeOrder() {
			var _this = this;
			console.log(_this.userType);
			if((_this.status==0 || _this.status==1 || _this.status==3 || _this.status==4 || _this.status=='s-0' || _this.status==7 || _this.status==6) && _this.isDel){
				if(_this.userType==1){
					uni.navigateTo({
						url:"../orderInfo/orderInfo?status="+_this.status+"&orderId="+_this.orderId
					})
				} else {
					uni.navigateTo({
						url:"../orderInfo_shipper/orderInfo_shipper?status="+_this.status+"&orderId="+_this.orderId
					})
				}
			}
		},
		orderInfo(){
			var _this = this;
			if(_this.userType==1){
				uni.navigateTo({
					url:"../orderInfo/orderInfo?status="+_this.status+"&orderId="+_this.orderId
				})
			} else {
				uni.navigateTo({
					url:"../orderInfo_shipper/orderInfo_shipper?status="+_this.status+"&orderId="+_this.orderId
				})
			}
		},
		delOrder() {
			this.isDel = false;
			this.$emit("delOrder");
			/* var _this = this;
			var url = this.serverURL;
			uni.showModal({
				title:'删除订单',
