									});
								}
							},
							fail(error) {
								console.log(error)
							}
						})
					}
				}
			}) */
			return false;
		},
		remarkOrder(isRemark) {
			this.isDel = false;
			uni.navigateTo({
				url: "../writeAassess/writeAassess?orderId="+_this.orderId
			})
		}
	}
}
